/*==============================================================
*												ONLINER_HEADER
=============================================================== */
.onliner-header {
  ul {
    padding: 0;
    margin: 0;
    list-style: none;
  }
}

.onliner-logo {
  max-height: 40px;
}

/*==============================================================
*												SECTION SALES SLIDER
=============================================================== */
.section-sales {
  .section-title {
    margin-bottom: 32px;
    @media (max-width: @max_xs) {
      margin-bottom: 16px;
    }
  }

  .sales-slider:not(.slick-initialized) {
    height: 500px;
    opacity: 0;
    overflow: hidden;
  }

  .sales-slider {
    transition: 0.3s;
  }

  .img img {
    width: 100%;
    height: auto;
  }

  .img {
    margin-bottom: 24px;
    @media (max-width: @max_xs) {
      margin-bottom: 16px;
    }
  }

  .desc {
    color: #333333;
  }

  .slick-dots {
    margin: 0;
    list-style: none;
    padding: 0;
    width: 100%;
    display: flex;
    padding-top: 24px;
    @media (max-width: @max_xs) {
      padding-top: 18px;
    }
    & > * {
      flex: 1;
      padding: 0 5px;
    }
    button {
      background-color: #c4c4c4;
      border: none;
      transition: 0.15s;
      &:focus {
        outline: none;
      }
      cursor: pointer;
      color: transparent;
      font-size: 0;
      padding: 0;
      line-height: 0;
      width: 100%;
      display: block;
      height: 4px;
    }

    @media (max-width: @max_sm) {
      padding-left: @col-p-sm;
      padding-right: @col-p-sm;
    }
  }

  .slick-active button {
    background-color: #ffdd22;
  }

  .slider-nav {
    display: block;
    width: 46px;
    height: 68px;
    cursor: pointer;

    transform: translateY(-50%);

    @media (max-width: @max_md) {
      width: 26px;
      height: 48px;
    }

    background-position: center center;
    background-repeat: no-repeat;
    background-size: 100% 100%;

    position: absolute;
    top: 13.5vw;

    @media (min-width: 1440px) {
      top: 162px;
      transform: none;
    }

    // @media (max-width: @max_xs) {
    //   top: 8vw;
    // }

    // @media (max-width: 420px) {
    //   top: 5vw;
    // }

    z-index: 10;
  }

  .prev-nav {
    left: 20px;
    background-image: url("../img/left.png");
    @media (max-width: @max_xs) {
      left: 10px;
    }
  }
  .next-nav {
    right: 20px;
    background-image: url("../img/right.png");
    @media (max-width: @max_xs) {
      right: 10px;
    }
  }

  .desc {
    line-height: 1.5;
  }

  .btn-row {
    padding-top: 16px;

    .btn {
      @media (max-width: @max_xs) {
        width: 100%;
      }
    }
  }
}

/*==============================================================
*    							FINANCIAL INSTRUMENTS SECTION
=============================================================== */
.section-fin-instruments {
  padding-top: 5px !important;
  .section-title {
    text-align: center;
  }
  .tabs-links-container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
    flex-wrap: wrap;
  }

  .tab {
    display: flex;
    background-color: #fff;
    box-shadow: 0px 4px 20px rgba(159, 159, 159, 0.25);

    padding: 40px 30px;

    @media (max-width: @max_sm) {
      flex-direction: column-reverse;
      padding: 25px;
      padding-bottom: 40px;
    }

    @media (max-width: @max_xs) {
      box-shadow: none;
      padding: 0;
    }
  }

  .desc-col {
    line-height: 1.5;
    padding-right: 30px;
    // display: flex;
    // flex-direction: column;
    @media (max-width: @max_sm) {
      padding-right: 0;
    }
    .btn-row {
      padding-top: 66px;
      @media (max-width: @max_sm) {
        padding-top: 32px;
      }
      @media (max-width: @max_xs) {
        padding-top: 16px;
        margin-bottom: 10px;
      }
    }
  }
  .img-col {
    padding-left: 30px;
    @media (max-width: @max_sm) {
      padding-left: 0;
      margin-bottom: 20px;
    }
    img {
      width: 100%;
      height: auto;
    }
  }
  .desc-col,
  .img-col {
    width: 50%;
    @media (max-width: @max_sm) {
      width: 100%;
    }
  }

  .desc-title {
    line-height: 1.3;

    @media (max-width: @max_xs) {
      font-size: 16px;
    }
  }

  .tab-link-item {
    padding: 0 3px;
    flex-shrink: 0;
    min-width: 140px;

    @media (max-width: @max_sm) {
      // min-width: 150px;
      min-width: auto;
    }
  }

  .tab-link {
    display: block;
    position: relative;
    padding: 15px;
    font-size: 20px;
    border-bottom: 4px solid #c4c4c4;
    transition: 0.15s;
    text-align: center;
    white-space: nowrap;
    @media (max-width: @max_sm) {
      font-size: 16px;
      padding: 13px;
    }
    @media (max-width: @max_xs) {
      padding-left: 10px;
      padding-right: 10px;
    }
    .bold {
      opacity: 0;
      font-weight: bold;
      display: none !important;
    }
    &.active {
      font-weight: bold;
    }
    .normal {
      display: block;
      // padding: 15px 30px;
      // position: absolute;
      // top: 0;
      // left: 0;
      // width: 100%;
      // height: 100%;
    }
    &.active {
      border-bottom-color: #ffdd22;
      // .bold {
      //   opacity: 1;
      // }
      // .normal {
      //   opacity: 0;
      // }
    }
  }
}

/*==============================================================
*												TEST DRIVE SECTION
=============================================================== */
.section-test-river {
  position: relative;
  padding-top: 80px !important;
  padding-bottom: 80px !important;
  .container {
    position: relative;
    z-index: 2;
    color: #fff;
  }

  .section-title {
    color: #fff;
  }
  .content-wrap {
    max-width: 665px;
  }

  .desc {
    line-height: 1.5;
    margin-bottom: 30px;
    @media (max-width: @max_xs) {
      margin-bottom: 12px;
    }
  }

  .section-title {
    margin-bottom: 30px;
    @media (max-width: @max_sm) {
      margin-bottom: 20px;
    }
    @media (max-width: @max_xs) {
      margin-bottom: 15px;
    }
  }
}

/*==============================================================
*												SECTION LINE UP
=============================================================== */
.section-lineup {
  padding-bottom: 0 !important;

  .container {
    @media (max-width: @max_md) {
      padding-left: 0 !important;
      padding-right: 0 !important;
    }
  }

  .ob-slider {
    transition: 0.3s;
  }

  .ob-slider:not(.slick-initialized) {
    opacity: 0;
    height: 500px;
    overflow: hidden;
  }

  .section-title {
    @media (max-width: @max_md) {
      padding-left: 20px;
    }
    @media (max-width: @max_sm) {
      padding-left: 15px;
    }
  }

  .touch & .slider-arrow {
    display: none !important;
  }

  .ab-col {
    padding: 13px 0;
  }

  .slick-track {
    padding-top: 0;
    padding-bottom: 10px;
    display: flex;
    align-items: stretch;
  }
  .slick-slide {
    height: inherit;
    float: none;
  }

  @media (max-width: @max_sm) {
    padding-bottom: 20px !important;
    & + .section {
      padding-top: 20px !important;
    }
  }

  .section-title {
    // margin-bottom: 15px;
    margin-bottom: 2px;
  }
  .slide {
    padding: 10px;
    @media (max-width: @max_md) {
      padding: 10px 5px;
    }
  }
  .ab-card {
    border-radius: 6px;
    padding: 20px 15px;
    // background-color: #fff;
    transition: 0.15s;
    display: block;
    height: 100%;
    &:hover {
      box-shadow: 0px 4px 20px rgba(159, 159, 159, 0.25);
    }
  }
  .info-row {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    font-size: 14px;
    &:last-child {
      margin-bottom: 0;
    }

    .svg-fill {
      fill: #c4c4c4;
    }
  }
  .title {
    margin: 0;
  }
  .year {
    margin-left: auto;
  }
  .ico {
    flex-shrink: 0;
    flex-grow: 0;
    margin-right: 10px;
  }
  .thumb {
    height: 13vw;
    margin-bottom: 20px;
    max-height: 190px;
    border-radius: 6px;
    overflow: hidden;
    @media (max-width: @max_xl) {
      height: 19vw;
    }
    @media (max-width: 767px) {
      height: 30vw;
    }
    @media (max-width: @max_xs) {
      height: 50vw;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .content {
    position: relative;
  }
  .price {
    position: absolute;
    bottom: 0;
    right: 0;
    text-align: right;
  }
  .top-price {
    margin-bottom: 10px;
  }

  .add-price {
    color: #c4c4c4;
    font-size: 12px;
  }
}

/*==============================================================
*												SECTION NEWS
=============================================================== */
.section-news {
  .row {
    margin-left: -12px !important;
    margin-right: -12px !important;
    margin-bottom: -40px !important;
    & > * {
      padding-left: 12px !important;
      padding-right: 12px !important;
      padding-bottom: 40px !important;
    }
    @media (max-width: @max_md) {
      margin-left: -10px !important;
      margin-right: -10px !important;
      margin-bottom: -40px !important;
      & > * {
        padding-left: 10px !important;
        padding-right: 10px !important;
        padding-bottom: 40px !important;
      }
    }

    @media (max-width: @max_xs) {
      margin-bottom: -24px !important;
      & > * {
        padding-bottom: 24px !important;
      }
    }
  }

  .title {
    line-height: 1.28;
  }

  .thumb {
    height: 15vw;
    max-height: 170px;
    margin-bottom: 10px;
    flex-shrink: 0;
    flex-grow: 0;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @media (max-width: @max_md) {
      height: 20vw;
    }
    @media (max-width: @max_sm) {
      height: 25vw;
    }
    @media (max-width: @max_xs) {
      height: 50px;
      width: 100px;
      margin-bottom: 22px;
    }
  }

  @media (min-width: @min_xs) {
    .title-wrap .date {
      display: none;
    }
  }

  @media (max-width: @max_xs) {
    .meta .date {
      display: none;
    }

    .title-wrap {
      padding-left: 10px;
      display: flex;
      flex-direction: column;
      .date {
        margin-top: auto;
        padding-bottom: 5px;
      }
    }
  }

  .news-card {
    position: relative;
    @media (min-width: @min_xs) {
      height: 100%;
      display: flex;
      flex-direction: column;
      .meta {
        margin-top: auto;
      }
    }
    @media (max-width: @max_xs) {
      display: flex;
    }
  }

  .video-ico {
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 2;
    @media (max-width: @max_xs) {
      top: 3px;
      left: 3px;
    }
  }

  .title a {
    color: #366db6;
    &:hover {
      opacity: 0.6;
    }
  }

  .title {
    margin-bottom: 10px;
  }

  .desc {
    line-height: 1.5;
    // max-height: 130px;
    height: 100%;
    overflow: hidden;
    position: relative;
    margin-bottom: 12px;
    color: #333;
    @media (max-width: @max_xs) {
      display: none;
    }
    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 70px;
      // background: linear-gradient(to top, #fff, transparent);
      background: linear-gradient(
        to top,
        rgba(255, 255, 255, 1),
        rgba(255, 255, 255, 0.1)
      );
      z-index: 5;
    }
  }

  .meta {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: @max_xs) {
      position: absolute;
      // bottom: 0;
      top: 53px;
      left: 0;
    }
  }

  .meta,
  .date {
    font-size: 13px;
    .retina(font-size, 10px);
    color: #666666;
    .svg-fill {
      fill: #666666;
    }
    @media (max-width: @max_xs) {
      font-size: 8px;
    }
  }

  .date {
    color: #9999;
  }

  .views {
    display: flex;
    align-items: center;
    padding: 3px 5px;
    background-color: #e7e7e7;
    border-radius: 3px;
    .ico {
      margin-left: 7px;
    }
    @media (max-width: @max_xs) {
      padding: 0 3px;
    }
  }

  .views .count {
    @media (max-width: @max_xs) {
      display: inline-block;
      padding-bottom: 1px;
    }
  }

  .btn-row {
    position: realtive;
    padding-top: 35px;
  }

  .card-content {
    max-height: 200px;
    display: flex;
    flex-direction: column;
  }
}

.logo-col a > * {
  max-width: none !important;
  max-height: 32px !important;
}

body .main .main-content > .section-sales,
body .main .main-content > .section-lineup {
  padding-top: 32px !important;
}
