body {
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: normal;
  background-color: #f4f4f4;
  .retina(font-size, 13px);
  @media (max-width: @max_xs) {
    font-size: 14px;
  }
  color: #333333;
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #000;
}
.compensate-for-scrollbar {
  margin: 0 !important;
}

a,
a:visited {
  color: #000;
  transition: 0.3s;
  text-decoration: none;
}

.condensed {
  font-family: "Open Sans Condensed";
}

.fancybox-content {
  max-height: none;
  overflow: visible;
}

a:hover {
  text-decoration: none !important;
}

a.bnt,
.btn,
button.btn,
input.btn,
.wpcf7-submit.btn {
  border: none;
  display: inline-block;
  padding: 15px 40px;
  font-weight: 600;
  font-size: 18px;
  border-radius: 2px;
  text-align: center;
  .retina(font-size, 15px);
  @media (max-width: @max_sm) {
    font-size: 14px;
    padding: 13.5px 32px;
  }

  &:focus {
    outline: none;
  }
  transition: 0.15s;
  cursor: pointer;

  &.blue-btn {
    color: #fff;
    background-color: #366db6;
    &:hover {
      background: linear-gradient(
          0deg,
          rgba(255, 255, 255, 0.1),
          rgba(255, 255, 255, 0.1)
        ),
        #366db6;
      box-shadow: inset 0px -2px 0px rgba(51, 51, 51, 0.2);
    }
    &.click {
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)),
        #366db6;
      box-shadow: inset 0px 2px 0px rgba(51, 51, 51, 0.2);
    }
  }

  &.white-btn {
    background-color: #fff;
    color: #1c1c1c;
    border: 1px solid #000000;
    &:hover {
      background: linear-gradient(
          0deg,
          rgba(0, 0, 0, 0.05),
          rgba(0, 0, 0, 0.05)
        ),
        #ffffff;
    }
    &.click {
      background: linear-gradient(
          0deg,
          rgba(0, 0, 0, 0.07),
          rgba(0, 0, 0, 0.07)
        ),
        #ffffff;
    }
  }
}

.bg-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.main-content {
  background: linear-gradient(180deg, #f4f4f4 0%, #e3e3e3 100%);
}
.main-content,
.like-main {
  // max-width: 1420px;
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
}

.white-bg {
  background-color: #fff;
}

::selection {
  background-color: rgba(54, 110, 182, 0.8);
  color: #fff;
}

.site-wrap {
  min-height: 100vh;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  & > * {
    width: 100%;
    flex-grow: 0;
    flex-shrink: 0;
  }
  .footer {
    margin-top: auto;
  }
}

.ico {
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;

  & > * {
    width: 20px !important;
    height: auto !important;
  }

  &.sm-ico {
    width: 18px;
    height: 18px;
    & > * {
      width: 18px !important;
      height: auto !important;
    }
    @media (max-width: @max_xs) {
      width: 12px !important;
      & > * {
        width: 12px !important;
      }
    }
  }

  &.ico-life > * {
    width: auto !important;
    height: 26px !important;
  }
}

.ico-left {
  position: relative;
  padding-left: 34px;
  .ico {
    position: absolute;
    left: 0;
    top: calc(50% - 24px / 2);
  }
}

.svg-fill,
.svg-stroke {
  transition: 0.3s;
}

.opacity-load {
  opacity: 0;
  transition: 0.5s;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
}

html {
  margin-top: 0 !important;
  overflow: visible;
}
body.admin-bar {
  padding-top: 32px;

  .dom-fixed-nav.nav-in-scroll .container .row {
    padding-top: 32px;
  }
}

.fancybox-content {
  @media (max-width: @max_sm) {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.disabled {
  opacity: 0.5;
  cursor: auto;
  pointer-events: none;
}

.section-title {
  &:extend(.condensed);
}

.show-more-row {
  @media (min-width: @min_xs) {
    display: none !important;
  }

  margin-bottom: 8px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  padding-top: 4px;

  a {
    color: #0066cc;
    font-size: 14px;
  }

  .show-more-link:not(.visible) .visible-text {
    display: none;
  }
  .show-more-link.visible .hidden-text {
    display: none;
  }
}

.mobile-hidden-content {
  @media (max-width: @max_xs) {
    max-height: 200px;
    position: relative;
    overflow: hidden;
    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 50px;
      background: linear-gradient(
        to top,
        rgba(255, 255, 255, 1),
        rgba(255, 255, 255, 0.1)
      );
      z-index: 5;
    }
    &.visible:after {
      display: none;
    }
  }
}

.section {
  padding-top: 60px !important;
  padding-bottom: 60px !important;
  &:first-child {
    padding-top: 50px !important;
  }
  @media (max-width: @max_md) {
    padding-top: 50px !important;
    padding-bottom: 50px !important;
  }
  &:first-child {
    @media (max-width: @max_xs) {
      padding-top: 30px !important;
    }
  }
}

.over-sm {
  @media (max-width: @max_sm) {
    transform: translateX(-@col-p-sm);
    width: calc(100% + @col-p-sm * 2);
  }
}
.col-p-sm {
  @media (max-width: @max_sm) {
    padding-left: @col-p-sm;
    padding-right: @col-p-sm;
  }
}

