.header {
  background-color: #f4f4f4;
}
.tab:not(.active) {
  display: none !important;
}

.m-bottom {
  margin-bottom: 30px;
}

.tag-bg {
  position: relative;
  overflow: visible;
  display: flex;
  align-items: center;

  padding: 7px 5px;
  display: inline-block;
  text-align: center;
  white-space: nowrap;
  color: #fff;
  padding-left: 12px;

  height: 36px;

  background-color: #7dbf26;
  position: relative;
  border-radius: 4px;
  .price-txt {
    position: relative;
    z-index: 1;
    display: block;
    padding-top: 1px;
    font-weight: bold;
    font-size: 16px;
  }

  &:before {
    border-radius: 4px;
    content: "";
    display: block;
    width: 26px;
    height: 26px;
    background-color: #7dbf26;
    position: absolute;
    top: 5px;
    left: -11px;
    transform: rotate(45deg);
  }

  &:after {
    content: "";
    position: absolute;
    right: 100%;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: #fff;
    top: 50%;
    transform: translate(50%, -50%);
  }
}

.cols-4 > * {
  width: 25%;
  @media (max-width: @max_md) {
    width: calc(100% / 3);
  }
  @media (max-width: @max_sm) {
    width: 50%;
  }
  @media (max-width: @max_xs) {
    width: 100%;
  }
}

.b-main-navigation {
  width: 100%;
}

.b-top-menu {
  background-color: #ffdd22;
  @media (max-width: 1000px) {
    background-color: #f4f4f4;
    border-bottom: 1px solid #d8d8d8;
  }
}

.onliner-footer {
  padding: 0;
  padding-top: 20px;
  padding-bottom: 40px;
}

.modal {
  display: none;
  width: 100%;
  max-width: 800px;
}

.modal-title {
  text-align: center;
  margin-bottom: 30px;
}

.form-row {
  display: flex;
  flex-wrap: wrap;
  margin: -20px;
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: -20px;
  }
  @media (max-width: @max_xs) {
    margin: -10px;
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: -10px;
    }
  }
}

.form-body {
  padding-bottom: 20px;
}

.form-col {
  padding: 20px;
  width: 100%;
  @media (max-width: @max_xs) {
    padding: 10px;
  }
}

.form-col-6 {
  width: 50%;
  @media (max-width: @max_sm) {
    width: 100%;
  }
}

.input {
  &:focus {
    outline: none;
  }
  padding: 18px 16px;
  width: 100%;
  border-radius: 2px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  &.has-error {
    border-color: #ea0500;
  }
}

.label {
  display: block;
  margin-bottom: 7px;
  font-weight: 600;
  @media (max-width: @max_xs) {
    font-size: 14px;
  }
}

textarea {
  max-width: 100%;
  min-width: 100%;
  min-height: 120px;
}

.required {
  display: inline-block;
  padding-left: 3px;
  font-weight: 14px;
  color: #ea0500;
}

.nice-select {
  width: 100%;
  height: auto;
  line-height: 1.5;
  padding: 18px 16px;
  .list {
    width: 100%;
    max-height: 240px;
    overflow: auto;
  }
}

.input-wrap {
  position: relative;
}

.input-help {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  padding-top: 3px;
  font-size: 12px;

  &.error {
    color: #ea0500;
  }
}

.js-model-select:not(.visible) {
  display: none;
}

.ajax-preloader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.7);
  transition: 0.15s;
  z-index: 20;
  & > * {
    width: 72px;
  }
}

.response-wrap {
  padding: 15px 20px;
  border-left: 2px solid transparent;
  .success & {
    border-color: #366db6;
  }
  .error & {
    border-color: #ea0500;
  }
}

.response-form ~ .btn-row {
  padding-top: 20px;
}

/*==============================================================
*												TOP BAR
=============================================================== */
.top-navbar {
  padding-top: 15px;
  padding-bottom: 15px;

  border-top: 1px solid #d8d8d8;
  border-bottom: 1px solid #d8d8d8;

  @media (max-width: @max_xs) {
    padding-top: 9px;
    padding-bottom: 9px;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow: auto;
  }
  .nav-col {
    margin-right: auto;
  }

  .over-content {
    width: 100%;
    // overflow: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .browser-ie & {
      height: 55px;
    }
  }

  .socials-loop {
    flex-wrap: nowrap;
  }

  .menu-item {
    white-space: nowrap;
  }
}
.top-menu {
  list-style: none;
  margin: 0;
  padding: 0 15px;

  display: flex;
  align-items: center;

  .open-modal-form {
    font-weight: bold;
  }

  .menu-item {
    padding: 0 15px;

    &:not(.open-modal-form) a {
      @media (max-width: 1279px) {
        font-weight: 600;
        color: #999999;
      }
    }
    &.open-modal-form a {
      @media (max-width: 1279px) {
        color: #999999;
      }
    }
  }

  a {
    color: #366db6;
    @media (max-width: @max_md) {
      color: #999999;
    }
    &:hover {
      opacity: 0.6;
    }
  }
  border-left: 1px solid #d8d8d8;
}

.logo-col {
  padding-right: 30px;
  a {
    display: block;
    & > * {
      max-width: 222px !important;
      @media (max-width: @max_sm) {
        max-width: 160px !important;
      }
    }
  }
}

/*==============================================================
*												SOCIALS LOOP
=============================================================== */
.socials-loop {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  & > * {
    margin: 5px 10px;
  }
  .svg-fill {
    fill: #002e6b;
  }
  .ico:hover .svg-fill {
    fill: #1c1c1c;
  }
}
