/*==============================================================
*												ONLINER_HEADER
=============================================================== */
.onliner-header ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.onliner-logo {
  max-height: 40px;
}
/*==============================================================
*												SECTION SALES SLIDER
=============================================================== */
.section-sales .section-title {
  margin-bottom: 32px;
}
@media (max-width: 575.9px) {
  .section-sales .section-title {
    margin-bottom: 16px;
  }
}
.section-sales .sales-slider:not(.slick-initialized) {
  height: 500px;
  opacity: 0;
  overflow: hidden;
}
.section-sales .sales-slider {
  transition: 0.3s;
}
.section-sales .img img {
  width: 100%;
  height: auto;
}
.section-sales .img {
  margin-bottom: 24px;
}
@media (max-width: 575.9px) {
  .section-sales .img {
    margin-bottom: 16px;
  }
}
.section-sales .desc {
  color: #333333;
}
.section-sales .slick-dots {
  margin: 0;
  list-style: none;
  padding: 0;
  width: 100%;
  display: flex;
  padding-top: 24px;
}
@media (max-width: 575.9px) {
  .section-sales .slick-dots {
    padding-top: 18px;
  }
}
.section-sales .slick-dots > * {
  flex: 1;
  padding: 0 5px;
}
.section-sales .slick-dots button {
  background-color: #c4c4c4;
  border: none;
  transition: 0.15s;
  cursor: pointer;
  color: transparent;
  font-size: 0;
  padding: 0;
  line-height: 0;
  width: 100%;
  display: block;
  height: 4px;
}
.section-sales .slick-dots button:focus {
  outline: none;
}
@media (max-width: 768.9px) {
  .section-sales .slick-dots {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.section-sales .slick-active button {
  background-color: #ffdd22;
}
.section-sales .slider-nav {
  display: block;
  width: 46px;
  height: 68px;
  cursor: pointer;
  transform: translateY(-50%);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: absolute;
  top: 13.5vw;
  z-index: 10;
}
@media (max-width: 991.9px) {
  .section-sales .slider-nav {
    width: 26px;
    height: 48px;
  }
}
@media (min-width: 1440px) {
  .section-sales .slider-nav {
    top: 162px;
    transform: none;
  }
}
.section-sales .prev-nav {
  left: 20px;
  background-image: url("../img/left.png");
}
@media (max-width: 575.9px) {
  .section-sales .prev-nav {
    left: 10px;
  }
}
.section-sales .next-nav {
  right: 20px;
  background-image: url("../img/right.png");
}
@media (max-width: 575.9px) {
  .section-sales .next-nav {
    right: 10px;
  }
}
.section-sales .desc {
  line-height: 1.5;
}
.section-sales .btn-row {
  padding-top: 16px;
}
@media (max-width: 575.9px) {
  .section-sales .btn-row .btn {
    width: 100%;
  }
}
